





































import Vue from 'vue';
import { Component } from 'vue-property-decorator';
import { createHelpers } from 'vuex-map-fields';

const { mapFields } = createHelpers({
  getterType: 'getField',
  mutationType: 'updateNumberField',
});

@Component({
  name: 'defence-input',
  computed: {
    ...mapFields([
      'defence.baseTN',
      'defence.counterPool',
      'defence.counterTN',
    ]),
  },
})
export default class DefenceInput extends Vue {}
